.stars {
  display: flex;
  height: 16px;
}

.stars__star {
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  height: 16px;
  width: 16px;
}

.stars--active :nth-child(-n+4) {
  background-image: url(../images/star-active.svg);
  margin-right: 4px;
}
